.profile {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-top: 5%;
}

.about {
  color: white;
  justify-content: left;
  margin: auto;
  align-items: left;
  text-align: left;
  max-width: 40%;
}

.about h2 {
  color: white;
}

.about h1 {
  color: #f60;
}

.photo {
  display: flex;
  margin-left: auto;
  margin-right: 15%;
  margin-top: 5%;
  border-radius: 50%;
  width: 20vw;
  height: 20vw;
  overflow: hidden;
  position: relative;
}

.photo img {
  position: absolute;
  bottom: 0;
  width: 20vw;
  right: 0%;
}
